@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600);@import url(https://fonts.googleapis.com/css?family=Lora);
.app-container {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
@media (max-width: 800px) {
.app-container {
    height: auto;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
}
}
.app-container .app-content {
  width: 100%;
  min-height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}
@media (max-width: 800px) {
.app-container .app-content {
    width: 100%;
    height: auto;
}
}
.app-container .app-content .app-content-vertically-center {
  padding: 80px;
  max-width: 640px;
  margin-top: auto;
  margin-bottom: auto;
}
@media (max-width: 1200px) {
.app-container .app-content .app-content-vertically-center {
    padding: 60px;
}
}
@media (max-width: 800px) {
.app-container .app-content .app-content-vertically-center {
    padding: 40px;
}
}
.intro {
  font-weight: 600;
  font-size: 64px;
  line-height: 80px;
  letter-spacing: -2px;
}
.intro h1 {
  font-weight: 400;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 16px;
  margin-bottom: 40px;
  letter-spacing: 0.4px;
  color: #47bec7;
}
.intro .tagline {
  color: #cccccc;
}
.my-bio {
  padding: 40px 0 40px 0;
  font-family: "Lora", serif;
}
@media (max-width: 1200px) {
.my-bio {
    padding: 30px 0;
}
}
@media (max-width: 800px) {
.my-bio {
    padding: 20px 0;
}
}
.my-bio p {
  color: #848d96;
}
.my-bio a {
  color: #848d96;
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
  border-bottom: 1px dotted #848d96;
}
.my-bio a:hover {
  color: #ca486d;
  -webkit-transition-duration: 0.1s;
          transition-duration: 0.1s;
  border-bottom: 1px dotted #ca486d;
}
.app-lists .app-list {
  width: 100%;
  display: inline-block;
  margin-bottom: 40px;
}
@media (max-width: 500px) {
.app-lists .app-list {
    width: 90%;
}
}
.app-lists .app-list h3 {
  font-weight: 400;
  text-transform: uppercase;
  font-size: 11px;
  line-height: 11px;
  margin-bottom: 31px;
  color: #848d96;
  letter-spacing: 2px;
  opacity: 0.5;
}
.app-lists .app-list ul li {
  font-size: 16px;
  line-height: 16px;
  margin-bottom: 20px;
}
.app-lists .app-list ul li a {
  color: #848d96;
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
}
.app-lists .app-list ul li a:hover {
  color: #ca486d;
  -webkit-transition-duration: 0.1s;
          transition-duration: 0.1s;
}
.app-lists .app-list ul li p {
  padding-top: 20px;
  /* margin-top: 10px; */
  font-size: 16px;
  line-height: 16px;
}
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
