.project-modal-content {
  overflow-y: hidden;
}
.modal-slide > img {
  max-width: 100%;
  max-height: 300px;
}

/* V-Modal */
.v--modal-overlay {
  background: rgba(0, 0, 0, 0.6);
}
.project-modal-content {
  height: auto;
  padding: 10px 20px 10px 20px;
}
.modal-header {
  height: 30%;
  font-size: 20px;
  font-weight: bold;
  padding-top: 10px;
  padding-bottom: 10px;
}
.modal-header > p {
  color: #000000 !important;
}
.modal-body {
  width: 100%;
  max-height: 70%;
}
.VueCarousel-slide-active {
